<template>
	<scroll-view class="content" scroll-y>
		<view class="content-item" v-for="content in item.data[item.tab].list" :key="content.momentId">
			<view class="avatar">
				<image class="avatar-img" :src="content.author.avatarUrl" mode="" lazy-load></image>
				<text class="nickname">{{content.author.nickname}}</text>
			</view>
			<view class="text">
				{{content.content}}
			</view>
			
			<view class="picture-1" v-if="content.images && content.images.length == 1">
				<image class="picture-item" :src="img" v-for="(img, i) in content.images" :key="img" lazy-load mode="widthFix" @click="clickImg(content.images, i)"></image>
			</view>
			<view class="picture-4" v-else-if="content.images && content.images.length === 4">
				<image class="picture-item" :src="img" v-for="(img, i) in content.images" :key="img" lazy-load mode="center" @click="clickImg(content.images, i)"></image>
			</view>
			<view class="picture" v-else>
				<image class="picture-item" :src="img" v-for="(img, i) in content.images" :key="img" lazy-load mode="center" @click="clickImg(content.images, i)"></image>
			</view>
			
			<view class="update-time">
				<text>{{toBeijing(content.updateTime)}}</text>
			</view>
			
			<view class="toolbar">
				<view class="iconfont icon-31zhuanfa"></view>
				<view class="iconfont icon-pinglun">{{content.commentCount}}</view>
				<view class="iconfont icon-dianzan" @click="like(content.momentId)">{{content.agree}}</view>
			</view>
		</view>
	</scroll-view>
</template>

<script>
	import { request } from '../../network/request.js'
	import { utcToBeijing } from '../../util/util.js'
	export default {
		name: 'Content',
		props: {
			item: {
				type: Object,
				default() {
					return {}
				}
			}
		},
		methods: {
			// 时间转换
			toBeijing(time) {
				return utcToBeijing(time)
			},
			
			// 预览图片
			clickImg(arr, item) {
				uni.previewImage({
					current: item,
					urls: arr,
					indicator: "number"
				})
			},
			
			// 点赞
			async like(id) {
				const res = await request({
					url: `/moment/${id}/like`,
					method: "POST"
				}, true)
				if(res.statusCode != 200) {
					uni.showToast({
						title: "请先登录",
						icon: "none"
					})
				}else {
					uni.showToast({
						title: res.data,
						icon: "none"
					})
				}
				
			}
		}
	}
</script>

<style scoped>
	.content {
		height: calc(100vh - 44px - 40px - 40px - 50px);
		overflow: scroll;
		background-color: #f5f5f7;
	}
	
	.content-item {
		padding: 10px 10px 0;
		background-color: #fff;
		margin-bottom: 5px;
	}
	
	.avatar {
		height: 30px;
		display: flex;
		line-height: 30px;
	}
	
	.avatar-img {
		width: 30px;
		height: 30px;
		border-radius: 50%;
		margin-right: 10px;
	}
	
	.nickname {
		font-size: 12px;
		color: #8a8a8a;
	}
	
	.text {
		font-size: 14px;
		margin: 10px 0;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}
	
	.picture, .picture-1, .picture-4 {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	
	.picture .picture-item, .picture-4 .picture-item {
		width: 32.66%;
		height: 150upx;
		margin-top: 1%;
		margin-right: 1%;
	}
	
	.picture .picture-item:nth-child(3n) {
		margin-right: 0%;
	}
	
	.picture-1 .picture-item {
		max-width: 100%;
		max-height: 300px;
	}
	
	.picture-4 .picture-item:nth-child(2n) {
		margin-right: 10%;
	}
	
	.update-time, .toolbar {
		color: #8a8a8a;
	}
	
	.update-time {
		font-size: 12px;
		padding: 10px 0;
	}
	
	.toolbar {
		display: flex;
		justify-content: space-around;
		width: 100%;
		height: 30px;
		border-top: 2px #f5f5f7 solid;
	}
	.iconfont {
		line-height: 30px;
		text-align: center;
	}
</style>
